import React from 'react';

class HistoryItem extends React.Component {
    render() {
        return(
            <section className='working-history-item'>
                <div>
                    <span>{ this.props.history.name }</span>&nbsp;({ this.props.history.from } ~ { this.props.history.to?this.props.history.to:'' })
                </div>
                <div>
                    <span></span>{this.props.history.job_title }
                </div>
                <div>
                    <span></span>{ this.props.history.description }
                </div>
                <div>
                    <span></span>{ this.props.history.comment }
                </div>
            </section>
        );
    }
}

export default HistoryItem;


