import React from 'react';

class ProjectItem extends React.Component { 
    render() { 
        return(
            <section className='project-history-item'>
                <div>
                    <span>{ this.props.project.name }</span>&nbsp;
                    ({ this.props.project.from } ~ { this.props.project.to?this.props.project.to:'' })
                </div>
                <div>
                    <span></span>{ this.props.project.environment }
                </div>
                <div>
                    <span></span>{ this.props.project.skills }
                </div>
                <div>
                    <span></span>{ this.props.project.tools }
                </div>
                <div>
                    <span></span>{ this.props.project.job }
                </div>
                <div>
                    <span></span>{ this.props.project.description }
                </div>
            </section>
        );
     }
 }

export default ProjectItem;


