import React from 'react';

import { 
    Box
 } from '../../../components';

import ProjectItem from './ProjectItem';
import HistoryItem from './HistoryItem';
import SkillItem from './SkillItem';
import TrainingItem from './TrainingItem';
import BaseInfoPanel from './BaseInfoPanel';

class BaseInfoContainer extends React.Component { 
    render() { 
        let { 
            userinfo = {},
            work_status = {},
            school = {},
            personal = '',
            skills = [],
            historys = [],
            projects = [],
            trainings = []
         } = this.props.detail || {};
        return (
            <Box size='18' className='body-container-baseinfo'>
                <div className='body-container-baseinfo-warp'>
                    <BaseInfoPanel title={  '' }>
                        <div><span></span>{ userinfo.name }</div>
                        <div><span></span>{ userinfo.short_name }</div>
                        <div><span></span>{ userinfo.sex }</div>
                        <div><span></span>{ userinfo.marry }</div>
                        <div><span></span>{ userinfo.address }</div>
                    </BaseInfoPanel>
                    <BaseInfoPanel title={ '' }>
                        <div><span></span>{ work_status.job_status }/{ work_status.job_type }</div>
                        {false&&<div><span></span>{ work_status.wanting }</div>}
                        <div><span></span>{ work_status.job_title }</div>
                        <div><span></span>{ work_status.job_skills }</div>
                        <div><span></span>{ work_status.focus_city }</div>
                        <div><span></span>{ work_status.experience }</div>
                        {false&&<div><span></span>{ work_status.current_salary }</div>}
                    </BaseInfoPanel>
                    <BaseInfoPanel title={ '' } className='baseinfo-personal-school'>
                        <div><span></span>{ school.college }</div>
                        <div><span></span>{ school.school_data }</div>
                        <div><span></span>{ school.specialization }</div>
                        <div><span></span>{ school.diploma }</div>
                        <div><span></span>{ school.comment }</div>
                    </BaseInfoPanel>
                    <BaseInfoPanel title={ '' }>
                        <div className='baseinfo-personal-item'
                            dangerouslySetInnerHTML={ {  __html: personal  } }/>
                    </BaseInfoPanel>
                    <BaseInfoPanel title={ '' }>
                        { 
                            skills.map((item, index) => { 
                                return <SkillItem
                                    key={ index + '' }
                                    skill={ item }
                                />
                             })
                         }
                    </BaseInfoPanel>
                    <BaseInfoPanel title={ '' }>
                        { 
                            historys.map((item, index) => { 
                                return <HistoryItem
                                    key={ index + '' }
                                    history={ item }
                                />
                             })
                         }
                    </BaseInfoPanel>
                    <BaseInfoPanel title={ '' }>
                        { 
                            projects.map((item, index) => { 
                                return <ProjectItem
                                    key={ index + '' }
                                    project={ item }
                                />
                             })
                         }
                    </BaseInfoPanel>
                    <BaseInfoPanel title={ '' }>
                        { 
                            trainings.map((item, index) => { 
                                return <TrainingItem
                                    key={ index + '' }
                                    training={ item }
                                />
                             })
                         }
                    </BaseInfoPanel>
                </div>
            </Box>
        );
     }
 }

export default BaseInfoContainer;


